<template>
  <div>
    <div class="topbar">
      <div class="rightorleft">
        <img v-if="navigation.nextLeft" src="../assets/left.png"/>
        <img v-else src="../assets/right.png"/>
      </div>
      <div class="navistatus">
        <div class="total">
          <div class="title"></div><div class="detail">: {{navigation.totalDistance}} {{Math.ceil(navigation.totalDistance/60)}}</div>
        </div>
        <div class="direction">
          {{navigation.nextDistance}}{{navigation.nextLeft?'  ':'  '}}
        </div>
      </div>
    </div>
    <div class='bottombar'>
      <div onClick={this.close}>
        <img src="../assets/quit.png" v-on:click="exit" width="20"/>
        <span class="line"/>
      </div>
      <div class="title" @click='birdlook'></div>
    </div>
  </div>
</template>

<script>

  import { mapGetters } from 'vuex'

  export default {
    name : 'navigation',
    computed: {
      ...mapGetters([
        'navigation'
      ])
    },
    methods: {
      exit() {

        this.$emit('stopnavigate')
      },
      birdlook() {

        this.$emit('birdlook')
      }
    }
  }

</script>

<style scoped lang="scss">

  .topbar {

    position: absolute;
    top: 0;
    width: 100%;
    background: #18202A;
    display: flex;
  }

  .rightorleft {

    > img {
      padding: 1rem;
      width: 3rem;
    }
  }

  .navistatus {

    padding-top: 1rem;
    flex: 1;

    .total {

      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;

      .title {

        color:#C8C8C8;
        font-size: 1rem;
      }

      .detail {

        font-size: 0.4rem;
        color: #C8C8C8;
        padding-right: 2rem;
      }
    }

    .direction {

      font-size: 1.5rem;
      color: #3984DD;
    }
  }

  .bottombar {

    position: absolute;
    bottom: 0;
    display: flex;
    height: 3rem;
    align-items: center;
    background: #18202A;
    width: 100%;
    text-align: center;

    > div:first-child {

      width: 5.6rem;
      height: 100%;
      position: absolute;
      display: flex;
      align-items: center;
      z-index: 100;
      justify-content: center;
    }

    > img {

      margin: 1rem;
      position: absolute;
      z-index: 100;
    }

    .line {

      width: 1px;
      height: 1.6rem;
      position: absolute;
      right: 0;
      background-color: #c8c8c8;
    }

    .title {

      position: absolute;
      width: 100%;
      color: #3984DD;
      line-height: 5.6rem;;
      left: 50%;
      transform: translateX(-50%);
    }
  }

</style>



