import idrDebug from './idrDebug'

function ajax(options) {
	//
	function setData() {
		var name, value;
		if (data) {
			if (typeof data === "string") {
				data = data.split("&");
				for (var i = 0, len = data.length; i < len; i++) {
					name = data[i].split("=")[0];
					value = data[i].split("=")[1];
					data[i] = encodeURIComponent(name) + "=" + encodeURIComponent(value);
				}
				console.log(data)
				console.log(typeof data)
				data = data.replace("/%20/g", "+");
			} else if (typeof data === "object") {
				var arr = [];
				for (var name in data) {
					if (typeof data[name] !== 'undefined') {
						var value = data[name].toString();
						name = encodeURIComponent(name);
						value = encodeURIComponent(value);
						arr.push(name + "=" + value);
					}
					
				}
				data = arr.join("&").replace("/%20/g", "+");
			}
			//getJSONPURL
			if (type === "get" || dataType === "jsonp") {
				url += url.indexOf("?") > -1 ? (url.indexOf("=")>-1 ? "&"+data : data ): "?" + data;
			}
		}
	}
	// JSONP
	function createJsonp() {
		var script = document.createElement("script"),
			timeName = new Date().getTime() + Math.round(Math.random() * 1000),
			callback = "JSONP_" + timeName;
		
		window[callback] = function(data) {
			clearTimeout(timeout_flag);
			document.body.removeChild(script);
			success(data);
		}
		script.src = url +  (url.indexOf("?") > -1 ? "&" : "?") + "callback=" + callback;
		script.type = "text/javascript";
		document.body.appendChild(script);
		setTime(callback, script);
	}
	//
	function setTime(callback, script) {
		if (timeOut !== undefined) {
			timeout_flag = setTimeout(function() {
				if (dataType === "jsonp") {
					// delete window[callback];
					document.body.removeChild(script);
					
				} else {
					timeout_bool = true;
					xhr && xhr.abort();
				}
				console.log("timeout");
				error && error('!');
				
			}, timeOut);
		}
	}
	// XHR
	function createXHR() {
		//IE6XMLHttpRequestMSXMLActiveX
		//XHR
		function getXHR() {
			if (window.XMLHttpRequest) {
				return new XMLHttpRequest();
			} else {
				//IEActiveX
				var versions = ["Microsoft", "msxm3", "msxml2", "msxml1"];
				for (var i = 0; i < versions.length; i++) {
					try {
						var version = versions[i] + ".XMLHTTP";
						return new ActiveXObject(version);
					} catch (e) {}
				}
			}
		}
		//
		xhr = getXHR();
		xhr.open(type, url, async);
		//
		if (type === "post" && !contentType) {
			//postcontent-Type application/x-www-four-urlencoded
			xhr.setRequestHeader("Content-Type", "application/x-www-form-urlencoded;charset=UTF-8");
		} else if (contentType) {
			xhr.setRequestHeader("Content-Type", contentType);
		}
		//
		xhr.onreadystatechange = function() {
			if (xhr.readyState === 4) {
				if (timeOut !== undefined) {
					//abort()onreadystatechange
					//timeout_bool
					if (timeout_bool) {
						return;
					}
					clearTimeout(timeout_flag);
				}
				if ((xhr.status >= 200 && xhr.status < 300) || xhr.status == 304) {
					
					success(xhr.responseText);
				} else {
					error(xhr.status, xhr.statusText);
				}
			}
		};
		//
		xhr.send(type === "get" ? null : data);
		setTime(); //
	}
	
	var url = options.url || "", //
		type = (options.type || "get").toLowerCase(), //,get
		data = options.data || null, //
		contentType = options.contentType || "", //
		dataType = options.dataType || "", //
		async = options.async === undefined && true, //true.
		timeOut = options.timeOut, //
		before = options.before || function() {}, //
		error = options.error || function() {}, //
		success = options.success || function() {}; //
	var timeout_bool = false, //
		timeout_flag = null, //
		xhr = null; //xhr
	setData();
	before();
	if (dataType === "jsonp") {
		createJsonp();
	} else {
		createXHR();
	}
};

function doAjax(url, data, successFn, failedFn) {
	
	console.log('' + url)
	
	if (data) {
		
		ajax({
			
			type: "get",
			
			dataType: 'jsonp',
			
			url: url, //
			
			data: data,
			
			timeOut: 10000,
			
			before:function () {
			
			},
			
			success:function (response) {
				
				// console.log('' + url)
				
				if (response != null && response.code == "success") {
					
					successFn && successFn(response)
				}
				else {
					
					if (failedFn) {
						
						failedFn && failedFn(response);
					}
					else {
						
						console.log(JSON.stringify(response))
					}
				}
			},
			
			error:function (response) {
				
				if (failedFn) {
					
					failedFn && failedFn(response);
				}
				else {
					
					console.log(JSON.stringify(response))
				}
			}
		});
	}
	else {
		
		ajax({
			
			type: "get",
			
			dataType: 'jsonp',
			
			url: url, //
			
			timeOut: 10000,
			
			before:function () {
			
			},
			
			success:function (response) {
				
				if (response != null && response.code == "success") {
					
					successFn && successFn(response)
				}
				else {
					
					if (failedFn) {
						
						failedFn && failedFn(response);
					}
					else {
						
						console.log(JSON.stringify(response))
					}
				}
			},
			
			error:function (response) {
				
				if (failedFn) {
					
					failedFn && failedFn(response);
				}
				else {
					
					console.log(JSON.stringify(response))
				}
			}
		});
	}
}

class idrNetworkManager {
	
	constructor() {
		
		this.host = 'https://wx.indoorun.com/'
		
		var ua = navigator.userAgent
		
		if (ua.match(/iPhone|iPod/i) != null) {
			
			this.osType = 'iPhone'
		}
		else if (ua.match(/Android/i) != null) {
			
			this.osType = 'Android'
		}
		else {
			
			this.osType = 'unknow'
		}
		
		this.sessionKey = null
		
		this.appId = null
		
		this.clientId = null
	}
	
	doAjax(url, data, successFn, failedFn) {
		
		data.appId = this.appId
		
		if (this.sessionKey) {
			
			data.sessionKey = this.sessionKey
		}
		
		if (this.clientId) {
			
			data.clientId = this.clientId
		}
		
		doAjax(url, data, successFn, failedFn)
	}
	
	serverCallWxAuth() {
		
		var url = this.host + 'wxauth/getAuthParas?reqUrl=' + window.location.href;
		
		return new Promise((resolve, reject)=>{
			
			doAjax(url, null, (res)=>{
				
				resolve(res)
			}, res=>{
				
				reject(res)
			})
		})
	}
	
	serverCallInitSession() {
		
		var str = 'appId=' + this.appId + '&clientId=' + this.clientId + '&time=null&sign=null'
		
		const reqUrl = this.host + 'wx/initSession.html?' + str;
		
		return new Promise((resolve, reject)=>{
			
			doAjax(reqUrl, {}, (res)=>{
				
				this.sessionKey = res.sessionKey
				
				resolve(res)
				
			}, (res)=>{
				
				reject(res)
			})
		})
	}
	
	serverCallWXSign(appId) {
		
		this.appId = appId
		
		var url = this.host + 'wx/getSign.html'
		
		return new Promise((resolve, reject)=>{
			
			this.doAjax(url, {appId}, (res)=>{
				
				this.clientId = res.clientId
				
				resolve(res)
				
			}, (res)=>{
				
				reject(res)
			})
		})
	}
	
	serverCallRegionPathData(regionId) {
		
		var url = this.host + 'wx/getRegionPathData';
		
		return new Promise((resolve, reject)=>{
			
			this.doAjax(url, {regionId}, res=>{
				
				resolve(res)
			}, res=>{
				
				reject(res)
			})
		})
	}
	
	serverCallRegionAllInfo(regionId) {
		
		var url = this.host + 'wx/getRegionData';
		
		return new Promise((resolve, reject)=>{
			
			this.doAjax(url, {regionId}, (res)=>{
				
				resolve(res)
				
			}, res=>{
				
				reject(res)
			})
		})
	}
	
	getMarkedUnit(regionId, success, failed) {
		
		var url = this.host + 'chene/getCheLocation.html'
		
		var data = {
			
			regionId
		}
		
		this.doAjax(url, data, success, failed)
	}
	
	removeMarkedUnit(regionId, success, failed) {
		
		var url = this.host + 'chene/removeCheLocation.html'
		
		var data = {
			
			regionId
		}
		
		this.doAjax(url, data, success, failed)
	}
	
	saveMarkedUnit(unit, success, failed) {
		
		var pos = unit.getPos()
		
		var unitInJson = JSON.stringify({svgX:pos.x, svgY:pos.y, floorId:unit.floorId, regionId:unit.regionId})
		
		var url = this.host + 'chene/saveCheLocation.html'
		
		var data = {
			'sName': unitInJson
		};
		
		this.doAjax(url, data, success, failed)
	}
	
	getParkingPlaceUnitByCarNo(carNo, regionId) {
		
		var url = this.host + 'chene/getParkingPlaceUnitByCarNo.html'
		
		var data = {
			regionId,
			carNo,
		}
		
		return new Promise((resolve, reject)=>{
			
			this.doAjax(url, data, (res)=>{
				
				resolve(res)
				
			}, (res)=>{
				
				reject(res)
			})
		})
	}
	
	parksOverview(regionId) {
	
		let data = {regionId}
		
		return new Promise((resolve, reject)=>{
			
			let url = this.host + 'thxz/pc/parksOverview'
			
			this.doAjax(url, data, res=>{
				
				resolve(res)
			}, res=> {
				
				reject(res)
			})
		})
	}
	
	serverCallLocatingBin({beacons, count, regionId, floorId}, success, failed) {
		
		var data = {
			'version':1,
			'beacons': beacons,
			'gzId': 'ewr2342342',
			'openId': 'wx_oBt8bt-1WMXu67NNZI-JUNQj6UAc',
			'OSType': this.osType,
			'regionId': regionId,
			'floorId': floorId,
			'beaconCount':count
		}
		
		if (!beacons) {
			
			data = {
				'version':1,
				'beacons': '',
				'gzId': 'ewr2342342',
				'openId': 'wx_oBt8bt-1WMXu67NNZI-JUNQj6UAc',
				'OSType': this.osType,
				'regionId': regionId,
				'floorId': floorId,
				'beaconCount':count
			}
		}
		
		var url = this.host + 'locate/locatingBin';
		
		this.doAjax(url, data, success, failed)
	}
}

export const networkInstance = new idrNetworkManager()


